    translate() {
      var a = this.latin;
      var b = this.ciril;

      if (!this.isLatin) {
        var tmp = a;
        a = b;
        b = tmp;
      }

      var replaceString = this.text;

      var regex;
      for (var i = 0; i < a.length; i++) {
        regex = new RegExp(a[i], "g");
        replaceString = replaceString.replace(regex, b[i]);
      }
      return replaceString;
    },
  },
